<template>
	<section>
		<button class="add_btn fin-search" v-show="!btnShow" v-on:click="btnShow=!btnShow"></button>
		<button class=" fin-search" v-show="btnShow" v-on:click="btnShow=!btnShow"></button>
		<div class="out" v-show="!btnShow">
				<table class="banner_infor">
					<tr>
		    			<td colspan="4">
		    				<h2></h2>
		    			</td>
		    		</tr>
					<tr>
						<td class="good_id" >id</td>
						<td class="createtime" >produce</td>
						<td class="set" ></td>
					</tr>
					<tr v-for="data in dataList">
						<td class="id">{{data.id}}</td>
						<td class="createtime" >{{data.produce}}</td>
						<td class="set" >
							<button class="fin-delete" v-on:click="delate_one(data)"></button>
						</td>
					</tr>
				</table>
		</div> 
		<div class="in" v-show="btnShow">
			<table class="in-infor">
				<tr>
					<td><span>id:</span><input type="text" class="goodId" placeholder="ID"></td>
				</tr>
				<button class="in_add fin-search" v-on:click="add_in(),btnShow=!btnShow"></button>
			</table>
		</div>
	</section>

</template>

<script>
import ser from "../js/serverUrl";
	export default {
		data:function(){
			return {
				btnShow: false,
				dataList:[],
				key:null
			}
		},
		methods:{
					
			//
			loadData : function(){
				var _this=this
				$.ajax({
					type: "POST",
					url :ser.url("getGoods"),
					dataType: 'json',
					success :function(resp){
				        _this.dataList=resp.data;
					}	
				})
			},

			// 
			delate_one:function(data){
				var _this=this
				this.key=data;				
				var arr={
					id:this.key.id,
					// produce:'',
				}
				console.log(arr);
				$.ajax({
					type: "POST",
					data:arr,
					url :ser.url("modifyGoodsProduce"),
					dataType: 'json',
					success:function(resp){
						_this.util.alertMsg(resp.msg);
						_this.loadData();
					}
				});
			},
			//
			add_in:function(){
				var _this=this
				var arr={
					id:$('.goodId').val(),
					produce:'',
				}
				console.log(arr);
				$.ajax({
					type: "POST",
					data:arr,
					url :ser.url("modifyGoods"),
					dataType: 'json',
					success:function(resp){
						_this.util.alertMsg(resp.msg);
						$('.goodId').val('');
						_this.loadData();
					}
				});
			}
		},
	    mounted(){
			this.loadData();
       	}
	}
</script>

<style scoped lang="scss">
	.banner_infor{
		margin-top: 10px;
		width:100%;
		border-top: 2px solid #D3D6DF;
		border-collapse:collapse;
		background-color:#fff;
		tr{
			td{
				text-align: center;
				padding: 5px 0;
				border:1px solid #ccc;
			}
		}
	}
	.in_re{
		text-indent: 5px;
		margin-left: 816px;
	}
	.in_pro{
		text-indent: 5px;
		margin-left: 816px;
	}
/*	button{
		margin-left: 10px;
	}*/
	.add_btn{
		width:100px;
	}
	.in-infor{
		margin-top: 10px;
		width:100%;
		border-top: 2px solid #D3D6DF;
		background-color: white;
		border-collapse: collapse;
		tr{
			td{
				height: 40px;
				line-height: 40px;
				border:1px solid #C1C4CB;
				padding-left: 10px;
				padding-right: 160px;
				input{
					width:62%;
					float: right;
					margin-top: 4px;
				}
			}
		}
	}
	.in_add{
		margin:10px 600px;
		width: 100px;
	}
</style>



