<template>
	<section class="pro_det">
		<!--  -->
		<table class="add_tab" >
			<tr>
				<td colspan="2">
					<h2></h2>
				</td>	
			</tr>
			<tr>
				<td class="name"></td>
				<td>
					<input type="text" v-model="adName" id ="adName" >
				</td>
			</tr>
			<tr>
				<td class="price"></td>
				<td>
					<input type="number" v-model="ad_price" id="ad_price">
				</td>
			</tr>
			<tr>
				<td></td>
				<td>
					<select class="ad_type"  v-model="ad_type">
						<option v-for="type in typeList" :value="type.id" >{{type.text}}</option>
					</select>
				</td>
			</tr>
			<tr>
				<td class="attr"></td>
				<td>
					<input type="text" name="" v-show="isAddAttrInput" v-model="newAttrVal">
					<button class="addattr fin-search" @click="isAddAttrInput?addAttr(newAttrVal):isAddAttrInput=true">{{isAddAttrInput?'':''}}</button>
					<div class="item_attr" v-for="(attr,index) in attrList" v-model="attrList">
						<span class="attr_name" @click="attr.isShowList=!attr.isShowList">
							{{attr.name}}
							<span></span>
							<div class="attr_value_list" v-show="attr.isShowList" >
								<!-- <div v-for="attrValue in attr.valueList" >
									{{attrValue.name}}
								</div> -->
							</div>
						</span>
						<button class="fin-delete" @click="deleteOneAttr(index)"></button>
						<button class="fin-search" @click="attr.isShowInput?addAttrValue(attr):attr.isShowInput=attr.isShowList=true" >{{attr.isShowInput?'':''}}</button>
						<input type="text" name="" v-show="attr.isShowInput" v-model="attr.inputVal">
					</div>
				</td>
			</tr>
			<tr>
				<td></td>
				<td class="add_number">
					<div v-for="(oneStock,key) in stockList">
						<input class="attr_value_num" type="number" name="" v-model="oneStock.number">
						<span v-for="oneVal in oneStock.attrValues">&nbsp;&nbsp;{{oneVal}}&nbsp;&nbsp;&nbsp;&nbsp;</span>
					</div>
				</td>
			</tr>
			<tr>
				<td class="status"></td>
				<td>
					<select id="ad_status" v-model="ad_status">
						<option></option>
						<option> </option>
					</select>
				</td>
			</tr>
			<tr>
				<td></td>
				<td>
					<select id="ad_product" v-model="ad_product">
						<option></option>
						<option></option>
					</select>
				</td>
			</tr>
			<tr>
				<td class="images"></td>
				<td>
					<div class="img_show" v-for="(imgSrc,index) in imageSrcList">
						<img class="img1" :src="imgSrc">
						<button class="fin-delete" @click="deleteImg(index)"></button>
						<!-- <span>..</span> -->
					</div>
					<div>
						<input type="file" id="ad_images" @change="uploadImgUrl($event)">
					</div>
				</td>
			</tr>
			<tr>
				<td class="introduce"></td>
				<td>
					<textarea id="ad_introduce" v-model="ad_introduce"></textarea>
				</td>
			</tr>
			<tr>
				<td ></td>
				<td>
					<vue-html5-editor id='' v-model="ad_content" :content="ad_content" :height="200"  ref="editor"></vue-html5-editor>
				</td>
			</tr>
			<tr>
				<td></td>
				<td>
					<div>
						<button class="all_back fin-search" @click="backClick"></button>
						<button class="edit_ok fin-search" @click="editOk"></button>
					</div>
				</td>
			</tr>
			
		</table>

	</section>
</template>

<script>
import ser from "../js/serverUrl";
import Vue from 'vue';
import VueHtml5Editor from 'vue-html5-editor';
	Vue.use(VueHtml5Editor, {
	    // new VueHtml5Editor(options)
	    // global component name
	    name: "vue-html5-editor",
	    // 
	    // if set true,will append module name to toolbar after icon
	    showModuleName: false,
	    // classfont-awesome
	    // custom icon class of built-in modules,default using font-awesome
	    icons: {
	        text: "fa fa-pencil",
	        color: "fa fa-paint-brush",
	        font: "fa fa-font",
	        align: "fa fa-align-justify",
	        list: "fa fa-list",
	        link: "fa fa-chain",
	        unlink: "fa fa-chain-broken",
	        tabulation: "fa fa-table",
	        image: "fa fa-file-image-o",
	        hr: "fa fa-minus",
	        eraser: "fa fa-eraser",
	        undo: "fa-undo fa",
	        "full-screen": "fa fa-arrows-alt",
	        info: "fa fa-info",
	    },
	    // 
	    // config image modulesss
	    image: {
	        // base64
	        // Url of the server-side,default null and convert image to base64
	        server: null,
	        // 
	        // the name for file field in multipart request
	        fieldName: "image",
	        //   max file size
	        // sizeLimit: 512 * 1024,
	        // truetruelocalResizeIMG
	        // default true,if set to true,the image will resize by localResizeIMG (https://github.com/think2011/localResizeIMG)
	        compress: true,
	        // 
	        // follows are options of localResizeIMG
	        // width: 960,
	        // height: 450,
	        // quality: 100,
	        // 
	        // handle response datareturn image url
	        uploadHandler(responseText){
	            //default accept json data like  {ok:false,msg:"unexpected"} or {ok:true,data:"image url"}
	            var json = JSON.parse(responseText)
	            if (!json.ok) {
	                alert(json.msg)
	            } else {
	                return json.data
	            }
	        }
	    },
	    // en-uszh-cn
	    //default en-us, en-us and zh-cn are built-in
	    language: "zh-cn",
	    // 
	    i18n: {
	        //specify your language here
	        "zh-cn": {
	            "align": "",
	            "image": "",
	            "list": "",
	            "link": "",
	            "unlink": "",
	            "table": "",
	            "font": "",
	            "full screen": "",
	            "text": "",
	            "eraser": "",
	            "info": "",
	            "color": "",
	            "please enter a url": "",
	            "create link": "",
	            "bold": "",
	            "italic": "",
	            "underline": "",
	            "strike through": "",
	            "subscript": "",
	            "superscript": "",
	            "heading": "",
	            "font name": "",
	            "font size": "",
	            "left justify": "",
	            "center justify": "",
	            "right justify": "",
	            "ordered list": "",
	            "unordered list": "",
	            "fore color": "",
	            "background color": "",
	            "row count": "",
	            "column count": "",
	            "save": "",
	            "upload": "",
	            "progress": "",
	            "unknown": "",
	            "please wait": "",
	            "error": "",
	            "abort": "",
	            "reset": ""
	        }
	    },
	    // 
	    // the modules you don't want
	    hiddenModules: [],
	    // 
	    // keep only the modules you want and customize the order.
	    // can be used with hiddenModules together
	    visibleModules: [
	        "text",
	        "color",
	        "font",
	        "align",
	        "list",
	        "link",
	        "unlink",
	        "tabulation",
	        "image",
	        "hr",
	        "eraser",
	        "undo",
	        "full-screen",
	        "info",
	    ],
	    // examples
	    // extended modules
	    modules: {
	        //omit,reference to source code of build-in modules
	    }
	})

	var vm = {
		data(){
			return {
				isAddAttrInput:false,
				isAttrValueInput:false,
				isAttrValueList:false,
				attrList:[],
				newAttrVal:"",
				dataList:[],
				imageSrcList:[],
				stockList:{},
				typeList:[],
				adName: '',
				ad_price:'',
				ad_status:'',
				ad_product:'',
				ad_introduce:'',
				ad_content:'',
				ad_type:''
			}
		},
		methods:{
			backClick:function(){
				this.$router.push({ path: 'productDetails', query: {}})
			},
			editOk:function(){
				if(confirm("")){
					var _this=this;
					function concatAttr(){
						var arr = [];
						for(var i=0;i<_this.attrList.length;i++){
							arr.push({name:_this.attrList[i].name,valueList:_this.attrList[i].valueList});
						}
						return arr;
					}
					function concatAttrValue(){
						// let router=this.router;
						var arr=[];	
						for(var key in _this.stockList){
							arr.push(_this.stockList[key]);
						}
						/*console.log(arr);*/
						return arr;
					}
					var tname = $('.pro_det #adName').val()
                    var tprice = $('.pro_det #ad_price').val()
                    var tintroduce = $('.pro_det #ad_introduce').val()
                    var tcontent = $('.pro_det .content').html()
                    var tnumber = concatAttrValue()
                    var timages = this.imageSrcList
                    var tattr = concatAttr()
					var addarr = {
						id:_this.$route.query.id,
						name:tname,
                        price:tprice,
						attr:tattr,
						number:tnumber,
						status:$('.pro_det #ad_status').val(),
						images:timages,
						introduce:tintroduce,
						/*produce:$('.pro_det #ad_product').val(),*/
						content:tcontent,
						cid:$('.ad_type option:selected').val()
					}
					console.log(addarr)
					if(tname !== "" && tprice !== "" && tintroduce !== "" && tcontent !== "" && tnumber.length>0 && timages.length>0 && tattr.length>0){
						$.ajax({
							type:'POST',
							url :ser.url("modifyGoods"),
							data:addarr,
							dataType: 'json',
							success:function(resp){
								_this.util.alertMsg(resp.msg)
								_this.$router.push({ path: 'productDetails'})
							}
						})
					}else{
						_this.util.alertMsg("")
					}
					
				}
				
			},
			deleteImg:function(index){
				var _this = this;

				$.ajax({
					type:'post',
					url :ser.url("delFileGoods"),
					data: {
						imgUrl:_this.imageSrcList[index],
						id:_this.$route.query.id
					},
					dataType: 'json',
					success:function(resp){
						if (resp.code == 200) {
							_this.imageSrcList.splice(index,1) ;
							_this.loadData();
						}   
	              	}
	          	})
			},
			uploadImgUrl:function(event){
				var formdata = new FormData();
				formdata.append("imgFile", event.currentTarget.files[0]);
				var _this=this;
				$.ajax({
					type:'post',
					url :ser.url("doUploadImgGoods"),
					data : formdata, 
					dataType: 'json',
					cache : false, 
					contentType : false, 
					processData : false, 
					config: {
						headers: {'Content-Type': 'multipart/form-data'}
					},
					success:function(resp){
						_this.imageSrcList.push(resp.data);
	              	}
	          	})
				return false;  
			},
			deleteOneAttr:function(index){
				var oneData = this.attrList.splice(index,1);
				this.stockList = {};
				this.concatStock();
			},
			addAttr:function(newAttr){
				this.isAddAttrInput = false;
				if(newAttr == ""){
					return;
				}
				this.attrList.push(
				{
					name:newAttr,
					isShowInput:false,
					isShowList:false,
					inputVal:"",
					valueList:[
					]
				}
				);
				this.newAttrVal = "";
			},
			concatStock:function(){
				var _this=this;
				function countNum(index,zuohekey){
					/*console.log("index : "+index);*/
					var list = _this.attrList[index].valueList;
					for(var i=0;i<list.length;i++){
						//
						/*console.log(_this.stockList[zuohekey+list[i].name])*/
						if(!_this.stockList[zuohekey+list[i].name]){
							_this.stockList[zuohekey+list[i].name]=zuohekey==""?
							{number:0,attrValues:[list[i].name]}:
							{number:0,attrValues:_this.stockList[zuohekey].attrValues.concat(list[i].name)}
						}
						//
						//[aabbcc][aabbdd]aabbdd
						if(i == list.length-1){
							delete _this.stockList[zuohekey];
						}
						if( index < _this.attrList.length-1){
							countNum(index+1,zuohekey+list[i].name)
						}
					}
				}
				if(this.attrList.length>0){
					countNum(0,"");
				}
			},
			addAttrValue:function(attr){
				if(attr.inputVal != ""){
					attr.valueList.push({name:attr.inputVal});
					attr.inputVal = "";
				}
				this.concatStock();
				attr.isShowInput=false;
			},
			produtType:function(){
				var _this = this;
				$.ajax({
					type:'POST',
					url :ser.url("getClassifyList"),
					dataType: 'json',
					success:function(resp){
						// var d=JSON.parse(data)
						_this.typeList=resp.data;
						_this.loadData();
					}
				})
			},
			loadData:function(data){
				var _this=this
				$.ajax({
					type: "POST",
					url :ser.url("getGoodsById"),
					data: {
						id: _this.$route.query.id
					},
					dataType: "json",
					success : function(resp){
						if (resp.code == 200) {
							_this.adName = resp.data.name
							_this.ad_price = resp.data.price
							_this.ad_type =resp.data.cid
							_this.ad_introduce = resp.data.introduce
							_this.ad_status = resp.data.status 
							_this.ad_product = resp.data.produce

							var attr = resp.data.attr;
							attr.forEach(function(item){
								item.isShowInput = false;
								item.isShowList = true;
							})
							_this.attrList = attr;

							var number = resp.data.number;
							number.forEach(function(item){
								_this.stockList[item.attrValues.join('')]= item;
							})
							_this.imageSrcList = resp.data.images
							_this.ad_content = resp.data.content
						}else{
							_this.util.alertMsg(rsp.msg)
						}
					}
				})
			}
		},
		mounted(){
			this.produtType();

			
		}
	};
	export default vm;
</script>

<style scoped lang="scss">
    *{
		font-family: '';
	}
	table{
		border:1px solid #ddd;
		background-color:#fff;
	}
	button{
		font-size: 12px;
		padding: 5px;
		margin: 5px auto;
	}
	
	.addproduct{
		width:80px;
		margin: 20px auto;
		right:200px;
	}
	
	.add_tab{
		width: 100%;
		text-align:left;
		line-height: 58px;
		tr{	
			td{
				.item_attr{
					button{
						margin-left: 10px;
					}
				}
				.attr_name{
					border: 1px solid #aaa;
					vertical-align: middle;
					padding: 5px;
					position: relative;
					.attr_value_list{
						min-width: 100%;
						box-sizing: content-box;
						position: absolute;
						top: 100%;
						margin: -1px;
						left: 0;
						border: 1px solid #aaa;
						background-color: #ffffc8;
						z-index: 1000;
						>div{
							white-space: nowrap;
						}
					}

				}
				.attr_value_num{
					width:100px;
					text-align: center;
				}
				*{
					vertical-align: middle;
				}
				.all_back,.edit_ok{
					width: 77px;
                    font-size: 14px;
				}

			}
			td:nth-child(1){
				text-align:center;
				vertical-align: top;
				width:10%;
			}
			.fin-delete{
					width:45px;
				}
			
		}
		#ad_introduce{
			width: 500px;
			height: 200px;
			margin:20px 0;
		}
		.img_show{
			display:inline-block;
			width:120px;
			text-align: center;
			img{
				width:70px;
				height:70px;
				border:none;
			}
			span{
				display:block;
				font-size:15px;
			}
		}
	}
</style>


